<template>
  <div class="index">
    <div class="index-swip">
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
        <van-swipe-item v-for="(item,index) in swipData" :key="index">
          <img :src="item.bannerImg" alt class="auto-img" @click="goDetail(item.pid)" />
          <div class="img-name">{{item.name}}</div>
        </van-swipe-item>
      </van-swipe>
    </div>
    <div class="index-welcome">
      <div class="clearfix">
        <div class="fr">
          <van-search shape="round" placeholder="请输入商品名称关键词" @focus="goSearch()"></van-search>
        </div>
        <div class="fl">
          <h2 class="title">{{timeText}},{{myData.nickName}}</h2>
          <p class="content">咖啡会让你脑洞大开哟</p>
        </div>
      </div>

      <div class="index-recommend">
        <h3>热门推荐</h3>
        <div class="recommend-content">
          <van-grid :column-num="3" :gutter="10" :border="false">
            <van-grid-item
              class="one-text"
              v-for="(item,index) in HotData"
              :key="index"
              @click="goDetail(item.pid)"
            >
              <div class="pro-img">
                <img class="auto-img" :src="item.smallImg" alt />
              </div>
              <div class="one-text">{{item.name}}</div>
              <div class="pro-price">￥{{item.price}}</div>
            </van-grid-item>
          </van-grid>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../mainViews/less/index.less";
export default {
  name: "Index",
  data() {
    return {
      timeText: "",
      swipData: [],
      HotData: [],
      myData: {},
    };
  },
  created() {
    this.getTime();
    this.getSwip();
    this.getHot();
    this.getMyname();
  },
  methods: {
    //获取当前时间，判断时候，早上，中午...
    getTime() {
      let hours = new Date().getHours();
      //
      if (hours >= 6 && hours < 12) {
        this.timeText = "早上好";
      } else if (hours >= 12 && hours < 18) {
        this.timeText = "下午好";
      } else if (hours >= 18 && hours < 24) {
        this.timeText = "晚上好";
      } else {
        this.timeText = "深夜好";
      }
    },
    //获取轮播图
    getSwip() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/banner",
        params: {
          appkey: this.appkey,
        },
      })
        .then((result) => {
          this.$toast.clear();
          // 
          if (result.data.code == 300) {
            this.swipData = result.data.result;
            //
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    //获取热门推荐
    getHot() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/typeProducts",
        params: {
          appkey: this.appkey,
          key: "isHot",
          value: 1,
        },
      })
        .then((result) => {
          this.$toast.clear();
          //
          if (result.data.code == 500) {
            this.HotData = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    //去到搜索界面
    goSearch() {
      this.$router.push({ name: "Search" });
    },
    //根据商品pid去到详情
    goDetail(pid) {
      this.$router.push({ name: "Detail", query: { pid: pid } });
    },
    //获取个人信息
    getMyname() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString: localStorage.getItem("WTPTK"),
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == "A001") {
            this.myData = result.data.result[0];
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
  },
};
</script>

<style lang="less" scoped>
</style>